.footer{
    height: 55px;
    width: 100%;
    position: fixed;
    bottom: 0px;
    border: 1px solid silver;
}
#i{
    color: silver;
    text-align: center;
    padding-left: 12px;
    margin-top: 10px;
    display: inline-block;
}
.f1{
    float: left;
    width: 30%;
    margin-left: 4px;
}
.f1 p{
    color: silver;
    font-size: 11px;
    margin-top: 4px;
    padding-left: 9px;
}
.tubiao{
    width: 40%;
    height: 100%;
    float: left;
}
.f2{
    width: 56%;
    height: 70%;
    float: left;
    margin-top: 8px;
    margin-left: 6px;
}
.f2 span {
    color: white;
    display: block;
    text-align: center;
    padding-top: 10px;
}
.join{
    height: 100%;
    width: 49%;
    background-image: linear-gradient(to right, #FFC500, #FF9402);
    float: left;
    overflow: hidden;
    border-top-left-radius: 25px;
    border-bottom-left-radius:25px
}
.buy{
    height: 100%;
    width: 49%;
    background-color: orangered;
    overflow: hidden;
    float: left;
    border-top-right-radius: 25px;
    border-bottom-right-radius:25px;
    margin-left: 1px;
}